import { AfterViewChecked, Component, ElementRef, HostBinding, OnInit, OnDestroy, ViewChild } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { RouteService } from "../service/index";
import { slideInUpAnimation } from "../animation/route.animation";

@Component({
  selector: 'test',
  templateUrl: './test.component.html',
  styleUrls: ['./test.component.css'],
  animations: [slideInUpAnimation]
})
export class TestComponent implements OnInit, AfterViewChecked, OnDestroy {

  @HostBinding('@routeAnimation') routeAnimation = true;
  @HostBinding('style.display') display = 'block';
  @HostBinding('style.position') position = 'absolute';
  @HostBinding('style.width') width = '100%';

  renderText: string;

  @ViewChild("render")
  render: ElementRef;

  constructor(private route: ActivatedRoute,
    private routeService: RouteService) {
  }

  ngOnInit(): void {
    const self = this;
    this.route.data.subscribe((data: { width: string, name: string }) => {
      self.renderText = data.name;
      self.width = data.width || self.width;
    });

    this.routeService.setCurrentRouteConfig({
      init: true,
      route: this.route.routeConfig
    });
  }

  ngOnDestroy() {
    this.routeService.setCurrentRouteConfig({
      init: false,
      route: this.route.routeConfig
    });
  }

  ngAfterViewChecked(): void {
    const self = this;
    this.route.data.subscribe((data: { color: string }) => {
      self.render.nativeElement.style.backgroundColor = data.color;
    });
  }

}



